import uniIcon from "@/components/uni-icon/uni-icon.vue"
import CommApi from "@/pages/comm/api.js"
import ContentInfo from "@/pages/comm/content-info.vue"

export default {
	data() {
		return {
			talkList: [],
			total: 0,
			screenHeight: null,
			scrollTop: 0,
			page: 1,
			old: {
				scrollTop: 0
			},
			value: false,
			replyInfo: '我是本周值班教师某某某，请问您想和我分享什么',
			userinfo: {},
			userType: null,
			currentItem: {},
		}
	},
	
	components: {
		uniIcon,
		ContentInfo
	},
	
	created() {
		uni.getSystemInfo({
			success: (res) => {
				this.screenHeight = res.screenHeight;
			}
		})
		this.getTalkList();
	},
	
	methods: {
		submitMessage(e) {
			console.log(this.value)
		},
		checkboxChange(e) {
			var items = this.items,
			values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
			    const item = items[i]
			    if(values.includes(item.value)){
					this.$set(item,'checked',true)
			    } else {
			        this.$set(item,'checked',false)
			    }
			}
			console.log(this.items)
		},
		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			if (this.talkList.length < this.total) {
				this.page = this.page + 1;
				this.getMoreTalkList()
			}
		},
		scroll: function(e) {
			// console.log(e, e.detail.scrollTop)
			this.old.scrollTop = e.detail.scrollTop
		},
		changeReply(obj) {
			if (obj.replyContent != null) {
				obj.webState = !obj.webState;
			} else {
				this.replyInfo = obj.content;
				this.currentItem = obj;
				this.value = true;
			}
		},
		talkClick() {
			this.value = true;
		},
		getTalkList() {
			uni.request({
				url: CommApi.api.getTalkList.real,
				data: JSON.stringify({
					teacherid: (this.userinfo && this.userinfo.id) || 1,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						const talkList = res.data.data;
						this.total = res.data.total;
						const len = talkList.length;
						for (let i=0; i< len; i++) {
							talkList[i].webState = false;
						}
						this.talkList = talkList;
						console.log(res.data.data)
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
		getMoreTalkList() {
			uni.request({
				url: CommApi.api.getTalkList.real,
				data: JSON.stringify({
					teacherid: (this.userinfo && this.userinfo.id) || 1,
					page: this.page,
					pageSize: 10,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						const talkList = res.data.data;
						this.total = res.data.total;
						const len = talkList.length;
						for (let i = 0; i < len; i++) {
							talkList[i].webState = false;
						}
						this.talkList = this.talkList.concat(talkList);
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
		onConfirm(item) {
			this.value = false;
			this.sendMassage(item);
		},
		sendMassage(item) {
			uni.request({
				url: CommApi.api.replyMassage.real,
				data: JSON.stringify({
					id: this.currentItem.id,
					replyContent: item.content,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						this.getTalkList();
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
	}
}
